body {
    #root {
        #app {
            #content {
                #landing {
                    &:before {
                        content: "";
                        position: fixed;
                        left: 0;
                        right: 0;
                        top: 0;
                        width: 100%;
                        height: 100%;
                        -webkit-filter: blur(3px);
                        -moz-filter: blur(3px);
                        -o-filter: blur(3px);
                        -ms-filter: blur(3px);
                        z-index: -1;
                        filter: blur(3px);
                        background: url(../assets/field.png) bottom no-repeat,
                            radial-gradient(circle, var(--blue) 50%, var(--white) 150%);
                    }

                    .main {
                        filter: blur(0) !important;
                        -webkit-filter: blur(0) !important;

                        .container {

                            .meme {
                                max-width: 300px;
                                margin: 0 auto;
                                background-color: rgba(0,0,0,0.5);
                                border-radius: 6px;
                            }

                            span {
                                font-family: "Nunito", sans-serif;
                                font-weight: 300;
                                text-transform: uppercase;

                                h1 {
                                    font-size: 3.75rem;
                                    text-align: center;
                                    color: var(--white);
                                }

                                h2 {
                                    text-align: center;
                                    color: var(--white);

                                    &.sub-title {
                                        font-size: 1.75rem;
                                        font-weight: 300;
                                        color: black;
                                    }
                                }

                                h3 {
                                    text-align: center;
                                    color: var(--white);

                                    &.sub-title {
                                        font-size: 1.5rem;
                                        font-weight: 300;
                                    }
                                }
                            }

                            #field {
                                display: block;
                                margin: auto;
                                display: inline-block;
                                position: relative;
                                width: 100%;
                                overflow: hidden;
                                padding: 2em;
                            }
                        }
                    }
                }
            }
        }
    }
}
